import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import mockupHippoMeowV1 from "../../images/mockupHippoMeowV1.png"
import hippoMeowV1FontsImage from "../../images/hippo-meow-v1-fonts.png"
import Work from "../../components/work"
import EmptyLine from "../../components/empty-line"

export default () => {
  const { hippoMeowV1 } = useStaticQuery(graphql`
    query {
      hippoMeowV1: file(relativePath: { eq: "hippoMeowV1.png" }) {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <Work
      title="Hippomeow"
      subTitle="Cake &amp; Cafe Shop Website"
      children={
        <React.Fragment>
          <div>I work on an idea for a family cake and cafe shop website.</div>
          <EmptyLine variant="Paragraph" />
          <div>
            My aim is to bring a fresh and friendly atmosphere for the homepage.
            Because this is a family shop, I want to tell the users a love story
            while they are scrolling up and down the page.
          </div>
          <EmptyLine variant="Paragraph" />
          <div>
            Users can easily and quickly scan through brief notes about the
            owners, place, and especially the menu of this shop. All necessary
            information is available on the homepage.
          </div>
        </React.Fragment>
      }
      mockupImage={mockupHippoMeowV1}
      colors={["#04a37a", "#c10202", "#494949", "#ffffff"]}
      fontImage={hippoMeowV1FontsImage}
      designFluidImages={[hippoMeowV1.childImageSharp.fluid]}
    />
  )
}
